*{
    margin:0;
    padding:0;
    text-decoration: none;

}
.container{
    display:flex;
    flex-direction: row;
    height: 100vh;
}

.sidebar{
   
    background: #7a7a7a;
    color: #181414;
    height: 100vh;
    width: 200px;
    transition: all 0.5s;
    transition: width 0.2s ease-in-out;
    

}
.top_section{
    display: flex;
    align-items:center;
    padding:20px 15px;
}
.logo{
    font-size: 30px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}
.link{
    display: flex;
    color: #11e2e9;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
}
.link:hover{
    background:lightskyblue;
    color: #000;
    transition: all 0.5s;
}
.active{
    background: lightskyblue;
    color: #000;
}
.icon, .link_text{
    font-size: 18px;
}
    
